import { ERROR_API, REQUEST_API, RESPONSE_API } from "../actions";

const INITIAL_STATE = {
  isFetching: false,
  data: {ip: '179.186.21.218', isp: "TELEFNICA BRASIL S.A", location: {city: "Vila Velha", postalCode: "", region: "Esprito Santo", timezone: "-03:00", lat: -20.32972, lng: -40.2925}},
  error: '',
}

export default function fetchAPI(state = INITIAL_STATE, action) {
  switch (action.type) {
    case REQUEST_API: 
      return {
        ...state,
        isFetching: true,
        error:'',
      }
    case RESPONSE_API: 
      return {
        ...state,
        data: action.payload,
        isFetching: false,
        error:'',
      }
    case ERROR_API: 
      return {
        ...state,
        error: action.error,
        isFetching: false,
      }
    default:
      return state;
    }
  }